import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import React, { Component } from 'react';
import './App.css';
import InputMask from 'react-input-mask';
import TextField from '@material-ui/core/TextField';
import HorizontalLinearStepper from './components/Step.js'
import Bar from './components/Bar.js'
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';

const theme = createMuiTheme({
    palette: {
        primary: {
            main: '#5b8cdb',
        },
        secondary: {
            main: '#3d4a5d'
        },
    },
})


class App extends Component {
    constructor(props){
        super(props)
        this.state = {
            'phone': '55',
            'message': '',
            'copied': false,
            'step': 1,
        }

        this.getNumber = this.getNumber.bind(this)
        this.getMessage = this.getMessage.bind(this)
        this.changeStep = this.changeStep.bind(this)
        this.hardReset = this.hardReset.bind(this)
    }

    hardReset(){
        this.setState({
            'step': 1,
            'phone': '55',
            'message': ''
        })
    }
    handleChange = name => event => {
        this.getNumber()
        this.getMessage()
        this.setState({
            [name]: event.target.value,
        });
    };

    getNumber(){
        let phone = this.state.phone.replace('+', '').replace('(', '').replace(')', '').replace('-', '').replace(/\s/g, "").replace(/_/g, '')
        return phone
    }

    getMessage(){
        let message = this.state.message.replace(/\s/g, "%20")
        return message
    }

    changeStep(step){
        console.log(step)
        this.setState({'step': step})
    }

    InputWpp(props){
        if (this.state.step === 1 ){
            return(
                <div>
                    <InputMask mask="+99 (99) 9 9999-9999" value={this.state.phone} name='number' onChange={this.handleChange('phone')}>
                        {(inputProps) =>
                            <TextField
                                id="outlined-number-input"
                                label="Número do Whatsapp que irá receber a menssagem"
                                className={'input'}
                                name="phone"
                                autoComplete="Whatsapp"
                                margin="normal"
                                variant="outlined"

                                />
                        }
                    </InputMask>
                    <TextField
                        id="outlined-multiline-flexible"
                        label="Menssagem"
                        multiline
                        rows="5"
                        rowsMax="150"
                        value={this.state.mutiline}
                        onChange={this.handleChange('message')}
                        className={'input messagem'}
                        margin="normal"
                        helperText="Insira sua menssagem"
                        variant="outlined"
                        />
                </div>
            )
        }
    }


    render() {

        return (
            <MuiThemeProvider theme={theme}>
                <div className="App">
                    <Bar />
                    <Grid container spacing={0}>
                        <Grid item s={8} xs={8} style={{'margin': '30px auto', }}>
                            <Typography component="h6" variant="display1" gutterBottom>
                                Gerador de link para Whatsapp
                                <Typography gutterBottom>
                                    {`
                                        Crie um link para facilitar o contato dos seus clientes a você.
                                    `}
                                </Typography>
                                </Typography>
                            </Grid>
                            <Grid item s={8} xs={8} style={{'margin': 'auto'}}>
                                {this.InputWpp()}
                                <HorizontalLinearStepper hardReset={this.hardReset} changeStep={this.changeStep} message={this.getMessage} number={this.getNumber} />
                            </Grid>
                        </Grid>
                    </div>
                </MuiThemeProvider>
            );
        }
    }
    export default App;
