const { BitlyClient } = require('bitly-react');
const bitly = new BitlyClient('70aad95167330319bcc6f32e5608ebfd20b64b72', {});

export default async function init(url) {
    let result;
    try {
        result = await bitly.shorten(url);
    } catch (e) {
        throw e;
    }

    return result;
}
